<template>
    <div class="planlist">
        <h2>日程安排</h2>
        <div class="add">
            <input type="text"  placeholder="输入想要添加的内容" @keyup.enter="enter" v-model.trim="text">
        </div>
        <div class="content" v-if="hobbys.length!=0">
            <PlanItemVue v-for="( item,  index) in hobbys" :key="index" :plan="item" 
        @change="change"
        @deleteplan="deleteplan"></PlanItemVue>
        </div>
        
        <footer>
            <span>我的日程！</span>
            <div class="detail">
                <input type="checkbox" ref="all" name="" id="" @change="alldone"  
                :checked="isAll==hobbys.length && isAll!=0">
                <span>已选择{{isAll}}/全部项目{{hobbys.length}}</span>
                <button class="clear" @click="clearall">清除全部项目</button>
            </div>
        </footer>
    </div>
</template>
<script>
import PlanItemVue from './PlanItem.vue';
export default {
    name: 'PlanList',
    components:{
        PlanItemVue
    },
    data(){
        return {
            text:''
        }
    },
    props:{
        hobbys:{
            type:Array,
            default(){
                return []
            }
        }
    },
    methods:{
        enter(){
           
            this.$emit('add',this.text)
            console.log(this.text)
            this.text=''
           
            
        },
        deleteplan(value){
            
            this.$emit('flush',value)
        },
        change(value){
            this.$emit('change',value)
        },
        alldone(){
            this.$emit('alldone',this.$refs.all.checked)
        },
        clearall(){
            this.$emit('clearall')
        }
    },
    computed:{
        isAll(){
            return this.hobbys.filter((item)=>
                item.done
            ).length
        }
    }
}
</script>
<style lang="less" scoped>
.planlist{  
    border:2px solid black;
    border-radius: 4px;
    padding: 10px;
    h2{
        text-align: center;
    }
    .content{
        border: 1px solid black;
        padding: 3px;
    }

    footer{
        padding:30px 10px 5px 10px;
        height: 100px;
        .detail{
            margin:5px;
            .clear{
                background-color: red;
                padding:2px;
                color:#fff;
                border-radius: 4px;
                margin-left:40px;
            }
        }
    }
    .add{

        margin-top: 30px;
        padding-bottom:10px;
        width:100%;
        input{
            width:100%;
            height: 30px;
            outline:none;
            border-radius: 3px;
        }
       
        
    }
}
</style>


