<template>
    <div class="planitem">
        <div class="contain">
            <div class="select">
                <input type="checkbox" name="爱好" id="" :checked="plan.done" @change="change">
                <span>{{plan.content}}</span>
            </div>
            <span>{{plan.describe}}</span>
            <button @click="deleteplan" >删除</button>
        </div>
    </div>
</template>
<script>
export default {
    name: 'PlanItem',
    props:{
        plan:{
            type:Object,
            default(){
                return {}
            }
        }
    },
    methods:{
        change(){
            this.$emit('change',this.plan)
        },
        deleteplan(){
            if(confirm('确定删除吗？')){
                this.$emit('deleteplan',this.plan)
            }
        }
    }
}
</script>
<style scoped lang="less">
.contain{
    display: flex;
    line-height: 30px;
    height:30px;
    padding:0px 5px;
    margin-top:5px;
    transition: all .5s;
    justify-content: space-between;
    .select{
        width:70px;

        overflow: hidden;
    }
    span{
        flex:1;
        text-align: center;
    }
    button{
        width:80px;
        color:#fff;
        font-size: .2em;
        transition: all .5s;
        opacity: 0;
        background-color: red;
        border-radius: 4px;
    }
    &:hover{
        background-color: rgba(100, 100, 100, .4);
        
    }
    &:hover button{
        opacity: 1;
    }
}
</style>



