<template>
    <div id="introcard" v-if="Object.keys(inforSelf).length!=0">
        <div class="wrapper">
            <div class="header">
                <img :src='inforSelf.data.headimg' alt="">

            </div>
            <div class="info">
                <h2> {{ inforSelf.data.username }}</h2>
                <div>
                    <span>邮箱：</span>{{ inforSelf.data.email }}
                </div>
                <div>
                    <span>性别：</span>{{ inforSelf.data.sex }}
                </div>
                <div>
                    <span>年龄：</span>{{ inforSelf.data.age }}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'introcard',
    props: {
        inforSelf: {
            type: Object,
            default() {
                return {}
            }
        }
    }
}
</script>
<style scoped lang="less">
#introcard {


    .wrapper {
        width: 300px;
        height: 300px;
        margin: auto;
        display: flex;
        flex-direction: column;
        border: 2px solid rgba(100, 100, 100, .4);
        padding: 5px;

        .header {
            height: 100px;
            text-align: center;

            img {
                width: 100px;
                height: 100px;
                border-radius: 50%;
            }


        }

        .info {
            flex: 1;
            display: flex;
            justify-content: space-around;
            flex-direction: column;
            text-align: center;
            padding:5px;
            

        }
    }
}
</style>


